.container {
    margin: 20px auto;
    position: relative;
    height: 140px;
    width: 140px;
    border: lightgray 2px solid;
    border-radius: 100%;
    background-color: white;
    background-image: radial-gradient(circle at center, white 0px, white 6px, transparent 6px, transparent 14px, white 14px, white 26px, transparent 26px, transparent 34px, white 34px, white 46px, transparent 46px, transparent 54px, white 54px, white 64px, transparent 64px, transparent 74px), radial-gradient(circle at center, transparent 10px, lightgray 10px, lightgray 11px, transparent 11px, transparent 30px, lightgray 30px, lightgray 31px, transparent 31px, transparent 50px, lightgray 50px, lightgray 51px, transparent 51px, transparent 70px, lightgray 70px, lightgray 71px, transparent 71px), linear-gradient(to right, transparent 50%, lightgray 50%, transparent 51%), linear-gradient(30deg, transparent 50%, lightgray 50%, transparent 51%), linear-gradient(-30deg, transparent 50%, lightgray 50%, transparent 51%);
    /* transform: scale(1.5); */
    opacity: .75;
  }
  .tri1 {
    border-style: solid;
    border-width: 30px 28px;
    border-color: transparent transparent rgba(117, 235, 183, 0.8) rgba(117, 235, 183, 0.8);
    -webkit-transform: matrix(0.8660254, -0.5, 0, 1, 0, 0);
            transform: matrix(0.8660254, -0.5, 0, 1, 0, 0);
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom;
    position: absolute;
    bottom: 50%;
    left: 50%;
    transition: all .3s;
  }
  .tri1:hover {
    border-color: transparent transparent rgba(200, 223, 73, 0.9) rgba(200, 223, 73, 0.9);
    -webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
            filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
    z-index: 5;
  }
