* {
  -webkit-transition: all 300ms ease;
  -moz-transition: all 300ms ease;
  transition: all 300ms ease;
}

.profile-page {
  position: relative;
  height: 100%;
  color: darkcyan;
}
.profile-page.banner-mode {
  overflow: hidden;
}

@keyframes profile-page-change {
  0% {
    height: 100%;
  }
  100% {
    height: 58px;
  }
}

.profile-body {
  position: relative;
  width: 100%;
}

@keyframes profile-body-change {
  0% {
    max-height: 100%;
  }
  100% {
    max-height: 60px;
  }
}

.profile-pic {
  width: 100%;
  display: inline-block;
}
.profile-pic.banner-mode {
  width: auto;
}

.profile-pic > img {
  overflow: hidden;
  width: 50%;
  margin: 25% 25%;
  border: 2px solid white;
  border-radius: 100%;
}
.profile-pic.banner-mode > img {
  margin: 0;
  width: 50px;
  margin: 4px;
}

.profile-info {
  position: relative;
  display: inline-block;
  width: calc(100% - 80px);
  padding: 10px;
  margin-left: 40px;
  margin-right: 40px;
  text-align: center;
}
.profile-info.banner-mode {
  margin-left: 60px;
  margin-right: 0px;
  text-align: left;
}
.profile-info-content {
  position: absolute;
  top: -400%;
  left: 0;
  width: 100%;
}
.profile-info-content.banner-mode {
  top: -60px;
}

.profile-info-content.banner-mode * {
  line-height: 1;
}
